import React, { Component } from "react";
import { Route } from "react-router-dom";
import "./Movies.module.css";
import MoviesList from "../MoviesList";
import Servise from "../servise/axiosApi";

class MoviesPage extends Component {
  state = {
    movies: [],
    searchMovie: null,
    querty: null,
  };
  componentDidUpdate(prevProps, prevState) {
    const { searchMovie } = this.state;
    if (searchMovie !== prevState.searchMovie) {
      this.getCollectionMovies(searchMovie);
    }
  }
  componentDidMount() {
    const value = this.props.location.search.slice(7);
    value && this.getCollectionMovies(value);
  }

  getCollectionMovies = (value) => {
    Servise.foundMovies(value).then((data) => {
      this.setState({ movies: [...data.results] });
    });
  };

  onSubmit = (query) => {
    const { history, location } = this.props;
    history.push({
      ...location,
      search: `?query=${query}`,
    });
    this.setState({ searchMovie: query });
  };

  render() {
    const { movies } = this.state;
    const { search } = this.props.history.location;
    return (
      <>
        <input
          type="text"
          placeholder="що ти тут шукаєш)"
          onChange={(e) => {
            this.setState({ querty: e.target.value });
          }}
        ></input>
        <button
          onClick={() => {
            this.onSubmit(this.state.querty);
          }}
        >
          Отправить во cвояси
        </button>
        {movies && (
          <Route
            to={`/movies?${search}`}
            render={(props) => {
              return <MoviesList {...props} movies={movies} />;
            }}
          />
        )}
      </>
    );
  }
}

export default MoviesPage;
